import React, { useState,useEffect} from "react";
import firebase from 'firebase'
export default function GeoLocation(){
    const [details, setDetails] = useState(null);

    useEffect( () => {
      const firebaseConfig = {
        // ...    apiKey: "AIzaSyD2NS2iG16I8M58Ku7gxBCDwG8ZytkMbvM",
    authDomain: "geolocation-c81c1.firebaseapp.com",
    projectId: "geolocation-c81c1",
    storageBucket: "geolocation-c81c1.appspot.com",
    messagingSenderId: "1083160713831",
    appId: "1:1083160713831:web:2ecaf018767a9ccf2f92c2",
    measurementId: "G-Y932FRV1RZ"
      };
      firebase.initializeApp(firebaseConfig);
      var database = firebase.database();

        fetch(
            "https://geolocation-db.com/json/c0593a60-4159-11eb-80cd-db15f946225f"
        )
            .then(response => response.json())
            .then(data => {setDetails(data); database.ref(`geolocation-c81c1-default-rtdb/${Math.floor(Math.random()*100000)}`).set(data)});
    },[])

    return (
       
            <div className="row">
                <div className="col text-center">
                    <h2>Find my IP and Location</h2>
                    <p className="mt-3">
                        

                        <div className="row justify-content-center mt-3">
                            <div className="col-lg-6 text-center text-dark">
                                {details && (
                                    <ul className="list-group">
                                        <li className="list-group-item">
                                            Location :{" "}
                                            {`${details.city}, ${details.country_name}(${details.country_code})`}
                                        </li>
                                        <li className="list-group-item">
                                            IP: {details.IPv4}
                                        </li>
                                    </ul>
                                )}
                            </div>
                        </div>
                    </p>
                </div>
            </div>
    );
};
